ckground: #ff8057;\n  display: inline-block;\n  height: 50px;\n  width: 50px;\n  line-height: 50px;\n  text-align: center;\n  color: #ffffff;\n  border-radius: 6px;\n  font-size: 30px;\n  position: relative;\n}\n.features-box .features-icon:before {\n  content: \"\";\n  height: 60px;\n  width: 60px;\n  background: rgba(255, 128, 87, 0.1);\n  position: absolute;\n  left: 7px;\n  top: -16px;\n  transform: rotate(20deg);\n  z-index: -1;\n  border-radius: 6px;\n}\n/* \n// 09. Counter\n*/\n.counter-box .counter-icon {\n  background: rgba(255, 255, 255, 0.02);\n  height: 90px;\n  width: 90px;\n  border-radius: 50%;\n  line-height: 90px;\n  text-align: center;\n  font-size: 46px;\n  box-shadow: 0 3px 0px rgba(255, 255, 255, 0.2);\n}\n/* \n// 10. Testimonial\n*/\n.testi-border {\n  background: #ff8057;\n  height: 2px;\n  width: 20%;\n}\n.owl-carousel .testi-content .testi-box {\n  box-shadow: 2px 2px 10px rgba(52, 58, 64, 0.08);\n  border-radius: 6px;\n}\n.owl-carousel .testi-content .testi-user img {\n  max-width: 48px;\n}\n.owl-carousel .owl-nav {\n  display: none;\n}\n.owl-carousel .owl-dot {\n  margin-top: 20px;\n}\n.owl-carousel .owl-dot:focus {\n  outline: none;\n}\n.owl-carousel.owl-theme .owl-dots .owl-dot span {\n  width: 14px;\n  height: 4px;\n  background-color: rgba(255, 128, 87, 0.4);\n}\n.owl-carousel.owl-theme .owl-dots .owl-dot.active span, .owl-carousel.owl-theme .owl-dots .owl-dot:hover span {\n  width: 20px;\n  height: 4px;\n  background-color: #ff8057;\n}\n/* \n// 11. Contact\n*/\n.form-control:focus {\n  box-shadow: none;\n  background-color: #f7f9fe;\n  border-color: #ff8057;\n}\n.form-control {\n  padding: 18px 14px;\n  font-size: 14px;\n  background-color: rgba(52, 58, 64, 0.01);\n  border: 1px solid rgba(157, 169, 187, 0.2);\n}\n.form-control::-moz-placeholder {\n  color: #afb9c8;\n  font-size: 14px;\n}\n.form-control:-ms-input-placeholder {\n  color: #afb9c8;\n  font-size: 14px;\n}\n.form-control::placeholder {\n  color: #afb9c8;\n  font-size: 14px;\n}\n.error {\n  margin: 8px 0px;\n  display: none;\n  color: #ee4a4a;\n}\n#ajaxsuccess {\n  font-size: 16px;\n  width: 100%;\n  display: none;\n  clear: both;\n  margin: 8px 0px;\n}\n.error_message {\n  padding: 10px;\n  margin-bottom: 20px;\n  text-align: center;\n  border: 2px solid #ee4a4a;\n  color: #ee4a4a;\n  border-radius: 5px;\n  font-size: 14px;\n}\n.contact-loader {\n  display: none;\n}\n/* \n// 12. Subscribe\n*/\n.subscribe-icon:after {\n  content: \"\";\n  font-family: \"Material Design Icons\";\n  position: absolute;\n  font-size: 24px;\n  right: 0;\n  color: #ff8057;\n  bottom: 50%;\n  transform: translateY(50%);\n}\n.subscribe-form input {\n  padding: 13px 20px;\n  width: 100%;\n  font-size: 17px;\n  color: #343a40 !important;\n  border: none;\n  outline: none !important;\n  padding-right: 180px;\n  padding-left: 30px;\n  background-color: rgba(255, 255, 255, 0.85);\n  border-radius: 30px;\n  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);\n}\n.subscribe-form input::-moz-placeholder {\n  color: #bbc3d0;\n  font-size: 15px;\n}\n.subscribe-form input:-ms-input-placeholder {\n  color: #bbc3d0;\n  font-size: 15px;\n}\n.subscribe-form input::placeholder {\n  color: #bbc3d0;\n  font-size: 15px;\n}\n.subscribe-form .btn {\n  position: absolute;\n  right: 0px;\n  outline: none !important;\n  border-radius: 30px;\n  padding: 13px 38px;\n  border-top-left-radius: 0;\n  border-bottom-left-radius: 0;\n  font-size: 16px;\n  box-shadow: none !important;\n}\n.subscribe-form form {\n  position: relative;\n  max-width: 600px;\n  margin: 0px auto;\n}\n/* \n// 13. Footer\n*/\n.footer {\n  padding: 60px 0 24px;\n  position: relative;\n}\n.footer .company-sub-menu li {\n  padding: 8px 0;\n}\n.footer .company-sub-menu li a {\n  transition: all 0.5s;\n  color: #b5becc;\n}\n.footer .company-sub-menu li a:hover {\n  color: #ff8057 !important;\n  margin-left: 6px;\n}\n.footer .footer-social-list a {\n  border: 1px solid #d9dee5;\n  height: 40px;\n  width: 40px;\n  display: inline-block;\n  border-radius: 50%;\n  line-height: 38px;\n  text-align: center;\n  color: #bbc3d0;\n  font-size: 18px;\n  transition: all 0.3s;\n}\n.footer .footer-social-list a:hover {\n  background: #ff8057;\n  color: #ffffff;\n  border-color: #ff8057;\n  box-shadow: 0 0 2px 7px rgba(255, 128, 87, 0.15);\n}\n/*\n *  Owl Carousel - Core\n */\n.owl-carousel {\n  display: none;\n  width: 100%;\n  -webkit-tap-highlight-color: transparent;\n  /* position relative and z-index fix webkit rendering fonts issue */\n  position: relative;\n  z-index: 1;\n}\n.owl-carousel .owl-stage {\n  position: relative;\n  touch-action: manipulation;\n  -moz-backface-visibility: hidden;\n  /* fix firefox animation glitch */\n}\n.owl-carousel .owl-stage:after {\n  content: \".\";\n  display: block;\n  clear: both;\n  visibility: hidden;\n  line-height: 0;\n  height: 0;\n}\n.owl-carousel .owl-stage-outer {\n  position: relative;\n  overflow: hidden;\n  /* fix for flashing background */\n  -webkit-transform: translate3d(0px, 0px, 0px);\n}\n.owl-carousel .owl-wrapper,\n.owl-carousel .owl-item {\n  -webkit-backface-visibility: hidden;\n  -moz-backface-visibility: hidden;\n  -ms-backface-visibility: hidden;\n  -webkit-transform: translate3d(0, 0, 0);\n  -moz-transform: translate3d(0, 0, 0);\n  -ms-transform: translate3d(0, 0, 0);\n}\n.owl-carousel .owl-item {\n  position: relative;\n  min-height: 1px;\n  float: left;\n  -webkit-backface-visibility: hidden;\n  -webkit-tap-highlight-color: transparent;\n  -webkit-touch-callout: none;\n}\n.owl-carousel .owl-item img {\n  display: block;\n  width: 100%;\n}\n.owl-carousel .owl-nav.disabled,\n.owl-carousel .owl-dots.disabled {\n  display: none;\n}\n.owl-carousel .owl-nav .owl-prev,\n.owl-carousel .owl-nav .owl-next,\n.owl-carousel .owl-dot {\n  cursor: pointer;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n.owl-carousel .owl-nav button.owl-prev,\n.owl-carousel .owl-nav button.owl-next,\n.owl-carousel button.owl-dot {\n  background: none;\n  color: inherit;\n  border: none;\n  padding: 0 !important;\n  font: inherit;\n}\n.owl-carousel.owl-loaded {\n  display: block;\n}\n.owl-carousel.owl-loading {\n  opacity: 0;\n  display: block;\n}\n.owl-carousel.owl-hidden {\n  opacity: 0;\n}\n.owl-carousel.owl-refresh .owl-item {\n  visibility: hidden;\n}\n.owl-carousel.owl-drag .owl-item {\n  touch-action: pan-y;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n.owl-carousel.owl-grab {\n  cursor: move;\n  cursor: -webkit-grab;\n  cursor: grab;\n}\n.owl-carousel.owl-rtl {\n  direction: rtl;\n}\n.owl-carousel.owl-rtl .owl-item {\n  float: right;\n}\n/* No Js */\n.no-js .owl-carousel {\n  display: block;\n}\n/*\n *  Owl Carousel - Animate Plugin\n */\n.owl-carousel .animated {\n  -webkit-animation-duration: 1000ms;\n          animation-duration: 1000ms;\n  -webkit-animation-fill-mode: both;\n          animation-fill-mode: both;\n}\n.owl-carousel .owl-animated-in {\n  z-index: 0;\n}\n.owl-carousel .owl-animated-out {\n  z-index: 1;\n}\n.owl-carousel .fadeOut {\n  -webkit-animation-name: fadeOut;\n          animation-name: fadeOut;\n}\n@-webkit-keyframes fadeOut {\n  0% {\n    opacity: 1;\n  }\n  100% {\n    opacity: 0;\n  }\n}\n@keyframes fadeOut {\n  0% {\n    opacity: 1;\n  }\n  100% {\n    opacity: 0;\n  }\n}\n/*\n * \tOwl Carousel - Auto Height Plugin\n */\n.owl-height {\n  transition: height 500ms ease-in-out;\n}\n/*\n * \tOwl Carousel - Lazy Load Plugin\n */\n.owl-carousel .owl-item {\n  /**\n  \tThis is introduced due to a bug in IE11 where lazy loading combined with autoheight plugin causes a wrong\n  \tcalculation of the height of the owl-item that breaks page layouts\n   */\n}\n.owl-carousel .owl-item .owl-lazy {\n  transition: opacity 400ms ease;\n}\n.owl-carousel .owl-item .owl-lazy[src^=\"\"], .owl-carousel .owl-item .owl-lazy:not([src]) {\n  max-height: 0;\n}\n.owl-carousel .owl-item img.owl-lazy 