           font-size: 15px;\r\n                transition: all 0.3s;\r\n                background-color: transparent !important;\r\n                padding: 6px 0;\r\n                margin: 0 20px;\r\n            }\r\n            .nav-link {\r\n                padding-right: 0px;\r\n                padding-left: 0px;\r\n            }\r\n        }\r\n        li.active a,\r\n        li a:hover,\r\n        li a:active {\r\n            color: $primary !important;\r\n        }\r\n    }\r\n    .navbar-toggles {\r\n        padding: 0 !important;\r\n        font-size: 18px;\r\n        background: 0 0;\r\n        border: 1px solid transparent;\r\n        color: $white;\r\n        outline: 0;\r\n    }\r\n}\r\n\r\n.navbar-toggler {\r\n    font-size: 24px;\r\n    margin-top: 5px;\r\n    margin-bottom: 0px;\r\n    color: $white;\r\n}\r\n\r\n.nav .open>a,\r\n.nav .open>a:focus,\r\n.nav .open>a:hover {\r\n    background-color: transparent;\r\n    border-color: $primary;\r\n}\r\n\r\n.menu-toggle {\r\n    padding: 4.5px 10px !important;\r\n    span {\r\n        line-height: 27px;\r\n    }\r\n}\r\n\r\n\r\n/*---NAVBAR STICKY--*/\r\n\r\n.nav-sticky {\r\n    &.navbar-custom {\r\n        margin-top: 0px;\r\n        background-color: $white;\r\n        box-shadow: 0 0 10px 0 rgba($black, 0.06);\r\n        color: $black !important;\r\n        .navbar-toggles {\r\n            padding: .25rem .75rem;\r\n            border: 1px solid transparent;\r\n            outline: 0;\r\n        }\r\n        .navbar-nav {\r\n            margin-top: 0px;\r\n            li {\r\n                a {\r\n                    color: darken($muted, 6%) !important;\r\n                }\r\n            }\r\n            li.active a,\r\n            li a:hover,\r\n            li a:active {\r\n                color: $primary !important;\r\n            }\r\n        }\r\n    }\r\n    .call-no {\r\n        a {\r\n            color: $warning !important;\r\n        }\r\n    }\r\n    .logo {\r\n        .logo-dark {\r\n            display: inline-block !important;\r\n        }\r\n        .logo-light {\r\n            display: none !important;\r\n        }\r\n    }\r\n}\r\n\r\n.navbar-toggler {\r\n    &:focus {\r\n        outline: none;\r\n    }\r\n}\r\n\r\n\r\n/*---NAVBAR LIGHT--*/\r\n\r\n.navbar-light {\r\n    .navbar-nav {\r\n        li {\r\n            a {\r\n                color: rgba($white, 0.7) !important\r\n            }\r\n        }\r\n        li.active a,\r\n        li a:hover,\r\n        li a:active {\r\n            color: $white !important;\r\n        }\r\n    }\r\n}\r\n\r\n\r\n/*---Responsive--*/\r\n\r\n@media (min-width: 200px) and (max-width: 768px) {\r\n    .navbar-custom {\r\n        margin-top: 0px;\r\n        background-color: $white !important;\r\n        box-shadow: 0 3px 10px rgba($black, 0.08);\r\n        color: $white !important;\r\n        .navbar-nav {\r\n            margin-top: 0px;\r\n            li {\r\n                a {\r\n                    transition: all 0.4s;\r\n                    color: $muted !important;\r\n                    margin: 0px;\r\n                }\r\n                &.active {\r\n                    a {\r\n                        border-color: transparent;\r\n                    }\r\n                }\r\n            }\r\n            li.active a,\r\n            li a:hover,\r\n            li a:active {\r\n                color: $primary !important;\r\n            }\r\n        }\r\n        &>.container {\r\n            width: 90%;\r\n        }\r\n        .logo {\r\n            .logo-dark {\r\n                display: inline-block !important;\r\n            }\r\n            .logo-light {\r\n                display: none !important;\r\n            }\r\n        }\r\n        &.navbar-light {\r\n            .navbar-nav {\r\n                li.active a,\r\n                li a:hover,\r\n                li a:active {\r\n                    color: $dark !important;\r\n                }\r\n            }\r\n        }\r\n        .call-no {\r\n            a {\r\n                padding-left: 0 !important;\r\n                color: $warning !important;\r\n            }\r\n        }\r\n    }\r\n    .navbar-toggler {\r\n        font-size: 24px;\r\n        margin-top: 0px;\r\n        margin-bottom: 0px;\r\n        color: $dark;\r\n    }\r\n    .navbar-light {\r\n        .nav-item {\r\n            .nav-link:after {\r\n                background: $dark;\r\n            }\r\n        }\r\n    }\r\n}","/* \r\n// 03.Components\r\n*/\r\n\r\n.btn {\r\n    border-radius: 4px;\r\n    padding: 12px 34px;\r\n    font-size: 15px !important;\r\n    transition: all 0.5s;\r\n    &:focus {\r\n        box-shadow: none !important;\r\n    }\r\n    &:hover {\r\n        transform: translateY(-2px);\r\n        transition: all 0.5s;\r\n    }\r\n    &.btn-lg {\r\n        padding: 10px 22px !important;\r\n        font-size: 18px !important;\r\n    }\r\n    &.btn-sm {\r\n        padding: 8px 16px !important;\r\n        font-size: 14px !important;\r\n    }\r\n    @each $color,\r\n    $value in $theme-colors {\r\n        &.btn-#{$color} {\r\n            background-color: #{$value} !important;\r\n            border: 1px solid #{$value} !important;\r\n            color: $white;\r\n            box-shadow: 0 16px 16px -8px rgba($black, 0.16);\r\n            &:hover,\r\n            &:focus,\r\n            &.focus,\r\n            &:active,\r\n            &.active {\r\n                background-color: #{darken($value, 3%)}!important;\r\n                border:1px solid #{darken($value, 3%)}!important;\r\n                color: $white !important;\r\n            }\r\n        }\r\n        &.btn-outline-#{$color} {\r\n            border: 1px solid #{$value} !important;\r\n            color: #{$value} !important;\r\n            &:hover,\r\n            &:focus,\r\n            &.focus,\r\n            &:active,\r\n            &.active {\r\n                background-color: #{$value} !important;\r\n                border:1px solid #{$value} !important;\r\n                box-shadow: 0 3px 10px rgba($value, 0.5);\r\n                color: $white !important;\r\n            }\r\n        }\r\n    }\r\n    &.btn-light {\r\n        background-color: $light;\r\n        border: 1px solid $light;\r\n        color: $dark !important;\r\n        &:hover,\r\n        &:focus,\r\n        &.focus,\r\n        &:active,\r\n        &.active {\r\n            background-color: lighten($light, 3%);\r\n            border: 1px solid lighten($light, 3%);\r\n            box-shadow: 0 0 0 2px rgba($light, .08);\r\n            color: $dark !important;\r\n        }\r\n    }\r\n    &.btn-outline-light {\r\n        border: 1px solid $light;\r\n        color: $light !important;\r\n        &:hover,\r\n        &:focus,\r\n        &.focus,\r\n        &:active,\r\n        &.active {\r\n            background-color: $light;\r\n            border: 1px solid $light;\r\n            color: $dark !important;\r\n        }\r\n    }\r\n}\r\n","/* \r\n// 04.Helper\r\n*/\r\n\r\n@each $color, $value in $theme-colors {\r\n    .text-#{$color} {\r\n        color: #{$value} !important;\r\n    }\r\n    .bg-#{$color} {\r\n        background-color: #{$value} !important;\r\n    }\r\n}\r\n.text-white-70 {\r\n    color: rgba($white, 0.70);\r\n}\r\n.text-lighten-warning {\r\n    color: rgba($warning, 0.50);\r\n}\r\n.bg-gradient-primary {\r\n    background: linear-gradient(-45deg, $primary 0%, $info 100%);\r\n}\r\n.section {\r\n    padding-top: 100px;\r\n    padding-bottom: 100px;\r\n    position: relative;\r\n}\r\n\r\n.bg-overlay {\r\n    background: $black;\r\n    position: absolute;\r\ndisplay: block;\r\n    top: 0;\r\n    left: 0;\r\n    height: 100%;\r\n    width: 100%;\r\n    &.op-75 {\r\n        opacity: 0.75;\r\n    }\r\n}\r\n.font-weight-medium {\r\n    font-weight: 500;\r\n}\r\n\r\n.line-height-1_6 {\r\n    line-height: 1.6;\r\n}\r\n.line-height-1_4 {\r\n    line-height: 1.4;\r\n}\r\n\r\n.f-12 {\r\n    font-size: 12px;\r\n}\r\n\r\n.f-13 {\r\n    font-size: 13px;\r\n}\r\n\r\n.f-14 {\r\n    font-size: 14px;\r\n}\r\n\r\n.f-15 {\r\n    font-size: 15px;\r\n}\r\n\r\n.f-16 {\r\n    font-size: 16px;\r\n}\r\n\r\n.f-17 {\r\n    font-size: 17px;\r\n}\r\n\r\n.f-18 {\r\n    font-size: 18px;\r\n}\r\n\r\n.f-19 {\r\n    font-size: 19px;\r\n}\r\n\r\n.f-20 {\r\n    font-size: 20px;\r\n}\r\n.right-icon {\r\n    line-height: 0;\r\n    font-size: 22px;\r\n}\r\n\r\n\r\n.hero-bottom-img {\r\n    position: absolute;\r\n    bottom: 0;\r\n    right: 0;\r\n    left: 0;\r\n}","/* \r\n// 05.Loader\r\n*/\r\n\r\n#preloader {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    background: linear-gradient(to top left, $primary 0%, $info 100%);\r\n    z-index: 9999999;\r\n  }\r\n  \r\n  #preloader #status {\r\n    position: absolute;\r\n    left: 50%;\r\n    top: 50%;\r\n    -webkit-transform: translateY(-50%);\r\n            transform: translateY(-50%);\r\n  }\r\n  \r\n  .spinner {\r\n    margin: 100px auto;\r\n    width: 40px;\r\n    height: 40px;\r\n    position: relative;\r\n    text-align: center;\r\n    \r\n    -webkit-animation: sk-rotate 2.0s infinite linear;\r\n    animation: sk-rotate 2.0s infinite linear;\r\n  }\r\n  \r\n  .dot1, .dot2 {\r\n    width: 60%;\r\n    height: 60%;\r\n    display: inline-block;\r\n    position: absolute;\r\n    top: 0;\r\n    background-color: $white;\r\n    border-radius: 100%;\r\n    \r\n    -webkit-animation: sk-bounce 2.0s infinite ease-in-out;\r\n    animation: sk-bounce 2.0s infinite ease-in-out;\r\n  }\r\n  \r\n  .dot2 {\r\n    top: auto;\r\n    bottom: 0;\r\n    -webkit-animation-delay: -1.0s;\r\n    animation-delay: -1.0s;\r\n  }\r\n  \r\n  @-webkit-keyframes sk-rotate { 100% { -webkit-transform: rotate(360deg) }}\r\n  @keyframes sk-rotate { 100% { transform: rotate(360deg); -webkit-transform: rotate(360deg) }}\r\n  \r\n  @-webkit-keyframes sk-bounce {\r\n    0%, 100% { -webkit-transform: scale(0.0) }\r\n    50% { -webkit-transform: scale(1.0) }\r\n  }\r\n  \r\n  @keyframes sk-bounce {\r\n    0%, 100% { \r\n      transform: scale(0.0);\r\n      -webkit-transform: scale(0.0);\r\n    } 50% { \r\n      transform: scale(1.0);\r\n      -webkit-transform: scale(1.0);\r\n    }\r\n  }","/* \r\n// 06. Hero\r\n*/\r\n\r\n@mixin hero-bg-overlay {\r\n    background-repeat: no-repeat;\r\n    display: block;\r\n    opacity: 1;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    height: 100%;\r\n    width: 100%;\r\n}\r\n\r\n// ***** Hero 1 ***** //\r\n.hero-1-bg {\r\n    padding: 170px 0 200px;\r\n}\r\n\r\n// ***** Hero 2 ***** //\r\n.hero-2-bg {\r\n    padding: 170px 0 180px;\r\n    .hero-2-content {\r\n        position: relative;\r\n        z-index: 1;\r\n    }\r\n    .hero-2-bg-overlay {\r\n        background-image: url(\"../images/hero-2-bg.png\");\r\n        background-position: center center;\r\n        @include hero-bg-overlay;\r\n    }\r\n}\r\n\r\n// ***** Hero 3 ***** //\r\n.hero-3-bg {\r\n    padding: 170px 0 0;\r\n    .hero-3-img {\r\n        margin-top: 90px;\r\n    }\r\n    .hero-3-bg-overlay {\r\n        background-image: url(\"../images/hero-3-bg.png\");\r\n        @include hero-bg-overlay;\r\n    }\r\n}\r\n\r\n// ***** Hero 4 ***** //\r\n.hero-4-bg {\r\n    padding: 210px 0 190px;\r\n    .hero-4-bg-overlay {\r\n        background-image: url(\"../images/hero-4-bg.png\");\r\n        background-position: center center;\r\n        @include hero-bg-overlay;\r\n    }\r\n    .hero-login-form {\r\n        box-shadow: 0 4px 14px 0px rgba($black, 0.1);\r\n        .registration-form {\r\n            label {\r\n                color: lighten($muted, 5%);\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n// ***** Hero 5 ***** //\r\n.hero-5-bg {\r\n    .bd-example {\r\n        z-index: 1;\r\n        .carousel-item {\r\n            padding: 280px 0 260px;\r\n        }\r\n        .carousel-indicators {\r\n            bottom: 50px;\r\n            li {\r\n                width: 10px;\r\n                height: 10px;\r\n                border-radius: 50%;\r\n                margin-right: 8px;\r\n                margin-left: 8px;\r\n            }\r\n            .active {\r\n                border: 4px solid transparent;\r\n                box-shadow: 0 0 1px $white;\r\n            }\r\n        }\r\n        .carousel-caption {\r\n            position: relative;\r\n            left: 0;\r\n            right: 0;\r\n        }\r\n    }\r\n}\r\n\r\n// ***** Hero 6 ***** //\r\n.hero-6-bg {\r\n    background-size: cover !important;\r\n    .hero-6-title-content {\r\n        position: relative;\r\n        z-index: 1;\r\n    }\r\n}\r\n\r\n// ***** Responsive ***** //\r\n@media (max-width: 576px) {\r\n    .hero-1-bg {\r\n        padding: 120px 0 70px;\r\n        .hero-1-title {\r\n            font-size: 38px !important;\r\n        }\r\n    }\r\n    .hero-2-title {\r\n        font-size: 34px;\r\n    }\r\n    .hero-3-title,\r\n    .hero-4-title {\r\n        font-size: 28px;\r\n    }\r\n}\r\n\r\n@media (max-width: 768px) {\r\n    .hero-2-bg {\r\n        padding: 130px 0;\r\n    }\r\n    .hero-4-bg {\r\n        padding: 130px 0 90px;\r\n    }\r\n    .hero-5-bg {\r\n        .carousel-item {\r\n            padding: 140px 0 !important;\r\n        }\r\n    }\r\n    .hero-6-bg {\r\n        height: auto !important;\r\n        padding: 130px 0 90px !important;\r\n    }\r\n}","/* \r\n// 07. Services\r\n*/\r\n\r\n.service-box {\r\n    border-radius: 10px;\r\n    padding: 28px 29px;\r\n    margin-bottom: 30px;\r\n    position: relative;\r\n    overflow: hidden;\r\n    transition: all 0.5s;\r\n    .service-icon {\r\n        font-size: 44px;\r\n        transition: all 0.5s;\r\n    }\r\n    .read-more-icon {\r\n        .right-icon {\r\n            font-size: 30px !important;\r\n            transition: all 0.5s;\r\n        }\r\n    }\r\n    &:before {\r\n        content: \"\";\r\n        height: 100px;\r\n        width: 100px;\r\n        position: absolute;\r\n        top: 0px;\r\n        left: 0px;\r\n        border-radius: 10%;\r\n        z-index: -1;\r\n        transition: all 0.5s;\r\n    }\r\n    &:after {\r\n        content: \"\";\r\n        height: 0px;\r\n        width: 0px;\r\n        position: absolute;\r\n        top: 0px;\r\n        left: 0px;\r\n        border-radius: 10%;\r\n        z-index: -1;\r\n        transition: all 0.5s;\r\n    }\r\n    &:hover {\r\n        .service-icon {\r\n            color: $white;\r\n        }\r\n        .service-title {\r\n            color: $white !important;\r\n        }\r\n        .right-icon {\r\n            color: $white;\r\n        }\r\n        .service-subtitle {\r\n            color: rgba($white, 0.7) !important;\r\n        }\r\n        &:before {\r\n            height: 100%;\r\n            width: 100%;\r\n            position: absolute;\r\n            bottom: 0;\r\n            right: 0;\r\n            top: 0;\r\n            left: 0;\r\n            border-radius: 0;\r\n        }\r\n        &:after {\r\n            background: rgba($white, 0.06);\r\n            height: 100px;\r\n        width: 100px;\r\n        }\r\n    }\r\n}\r\n\r\n@each $color,\r\n$value in $theme-colors {\r\n    .service-#{$color} {\r\n        .service-icon, .right-icon {\r\n            color: #{$value};\r\n        }\r\n        &:before {\r\n            background: rgba($value, 0.05);\r\n        }\r\n        &:hover {\r\n            &:before {\r\n                background: #{$value};\r\n            }\r\n        }\r\n    }\r\n}","/* \r\n// 08. Features\r\n*/\r\n\r\n.features-box {\r\n    background: rgba($white, 0.025);\r\n    border-radius: 6px;\r\n    .features-icon {\r\n        background: $warning;\r\n        display: inline-block;\r\n        height: 50px;\r\n        width: 50px;\r\n        line-height: 50px;\r\n        text-align: center;\r\n        color: $white;\r\n        border-radius: 6px;\r\n        font-size: 30px;\r\n        position: relative;\r\n        &:before {\r\n            content: \"\";\r\n            height: 60px;\r\n            width: 60px;\r\n